.weatherContainer {
  width: 100%;
  background-color: #ffffff;
  border-radius: 12px;
  padding: 16px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
  margin-bottom: 20px;
}

.weatherTitle {
  font-size: 18px;
  font-weight: 600;
  margin: 0 0 4px 0;
  color: #333;
}

.locationName {
  font-size: 14px;
  color: #666;
  margin: 0 0 16px 0;
}

.forecastContainer {
  display: flex;
  overflow-x: auto;
  gap: 0;
  padding: 4px 0 12px 0;
  width: 100%;
  /* margin: 0 -16px; */
  padding: 0 16px;
  -webkit-overflow-scrolling: touch;
  scrollbar-width: none;
  -ms-overflow-style: none;
}

.forecastContainer::-webkit-scrollbar {
  display: none;
}

.weatherItem {
  flex: 0 0 auto;
  min-width: 80px;
  padding: 12px 8px;
  background-color: #f8f9fa;
  text-align: center;
  box-shadow: none;
  border-left: 1px solid #e9ecef;
  border-radius: 0;
}

.weatherItem:first-child {
  border-left: none;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}

.weatherItem:last-child {
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}

.timeLabel {
  font-size: 13px;
  font-weight: 500;
  margin: 0 0 8px 0;
  color: #495057;
}

.weatherIcon {
  width: 50px;
  height: 50px;
  margin: 0 auto;
}

.weatherIcon img {
  width: 100%;
  height: 100%;
}

.temperature {
  font-size: 20px;
  font-weight: 700;
  margin: 4px 0;
  color: #212529;
}

.description {
  font-size: 12px;
  color: #6c757d;
  margin: 0 0 8px 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
}

.weatherDetails {
  text-align: left;
  margin-top: 8px;
  font-size: 11px;
}

.detail {
  margin: 2px 0;
  display: flex;
  justify-content: space-between;
  color: #6c757d;
}

.detailLabel {
  font-weight: 500;
}

.detailValue {
  font-weight: 400;
}

@media (min-width: 768px) {
  .forecastContainer {
    gap: 0;
  }
  
  .weatherItem {
    min-width: 90px;
  }
} 